import cookies from 'js-cookie';
import { EventEmitter } from 'eventemitter3';
import { eloPack, eloUnpack, STRING, UINT8 } from '@elofun/node-packet';
import { Buffer } from 'buffer/';
import { EWSGWMsgTypes, IWSGWPackage, WSGWPackageSchema } from './schema';

const emitter = new EventEmitter();
const connect = (endpoint: string) =>
  new Promise((resolve) => {
    switch (ctx.ws?.readyState) {
      case WebSocket.CONNECTING:
      case WebSocket.OPEN: {
        return ctx.initialized.then(resolve);
      }

      default: {
        if (window.URLSearchParams) {
          const param = new URLSearchParams(window.location.search);
          if (param && param.get('host')) {
            endpoint = param.get('host') || endpoint;
          }
        }
        const ws = new WebSocket(endpoint);
        const authorized = new Promise((resolve, reject) => {
          emitter.on('auth', (data) => {
            // console.log(data);
            return resolve(data);
          });
          // emitter.on('authError', reject);
        });
        const heartbeatInterval = setInterval(() => {
          if (ws.readyState === WebSocket.OPEN) {
            ws.send(
              eloPack(
                { heartbeat: UINT8 },
                { heartbeat: ~~(Math.random() * 255) }
                //
              )
            );
          }
        }, 30000);
        ctx.ws = ws;
        ctx.initialized = new Promise((resolve) => emitter.on('initialized', resolve));

        ws.binaryType = 'arraybuffer';
        ws.onopen = () => {
          ws.send(
            eloPack(
              { userAgent: STRING, href: STRING, token: STRING },
              {
                userAgent: window.navigator.userAgent,
                href: window.location.href,
                token: cookies.get('token') ?? '',
              }
            )
          );
          ws.onmessage = (event: MessageEvent) => {
            const packet: IWSGWPackage = eloUnpack(WSGWPackageSchema, Buffer.from(event.data));
            const { type, data } = packet;
            switch (type) {
              case EWSGWMsgTypes.CONFIG: {
                setTimeout(
                  Function(`
                    "use strict";
                    const [{ ws, authorized, emitter, resolve }] = arguments;
                    ${data.toString()};
                    emitter.emit('initialized', { wsgw });
                    resolve({ wsgw })
                  `),
                  0,
                  { ws, authorized, emitter, resolve }
                );
                break;
              }
              case EWSGWMsgTypes.EVENT: {
                const event = JSON.parse(data.toString());
                emitter.emit(event.type, event.data);
                break;
              }
              default: {
                emitter.emit(EWSGWMsgTypes[type], data);
                break;
              }
            }
          };
        };
        ws.onclose = () => clearInterval(heartbeatInterval);
        ws.onerror = () => ws.close();
      }
    }
  });

export const connectWsgw = async (endpoint: string) => ({
  login: (service: string, api: string, data: any = {}, timeout: number = 60000) =>
    connect(endpoint).then(({ wsgw }: any) => wsgw.login(service, api, data, timeout)),
  request: (service: string, api: string, data: any = {}, timeout: number = 60000) =>
    connect(endpoint).then(({ wsgw }: any) => wsgw.request(service, api, data, timeout)),
  subscribe: (service: string, channel: string, func: (...args: any[]) => void) =>
    connect(endpoint).then(({ wsgw }: any) => wsgw.subscribe(service, channel, func)),
  getInfo: () => connect(endpoint).then(({ wsgw }: any) => wsgw.getInfo()),
  status: () => connect(endpoint).then(({ wsgw }: any) => wsgw.status()),
});

const ctx: any = {};
