import { STRING, BINARY, UINT16, UINT32, UINT8 } from '@elofun/node-packet';
import { Buffer } from 'buffer/';

export enum EWSGWMsgTypes {
  AUTH = 0,
  INFO,
  REQUEST,
  RESPONSE,
  NOTIFY,
  EVAL,
  SUBSCRIBE,
  BROADCAST,
  ACCESS_DENIED,
  EVENT,
  CONFIG,
}
export interface IWSGWPackage {
  type: EWSGWMsgTypes;
  data: Buffer;
}

export const WSGWPackageSchema = {
  type: UINT8,
  data: BINARY,
};

export interface IWSGWAuth {
  type: Buffer;
  credential: Buffer;
  secret: Buffer;
  clientId: Buffer;
}

export interface IWSGWInfo {
  type: Buffer;
  credential: Buffer;
  userId: Buffer;
  clientId: Buffer;
  scopes: Buffer[];
}

export const WSGWAuthSchema = {
  type: BINARY,
  credential: BINARY,
  secret: BINARY,
  clientId: BINARY,
};

export const WSGWInfoSchema = {
  type: BINARY,
  credential: BINARY,
  userId: UINT32,
  clientId: BINARY,
  scopes: [BINARY],
};

export interface IWSGWRequest {
  reqId: string;
  service: string;
  api: string;
  payload: Buffer;
  timeout: number;
}

export const WSGWRequestSchema = {
  reqId: STRING,
  service: STRING,
  api: STRING,
  payload: BINARY,
  timeout: UINT32,
};

export interface IWSGWResponse {
  reqId: string;
  data: Buffer;
  status: number;
}

export const WSGWResponseSchema = {
  reqId: STRING,
  data: BINARY,
  status: UINT16,
};
export interface IWSGEval {
  script: Buffer;
}

export const WSGWEval = {
  script: BINARY,
};

export interface IWSGWSubscribeRequest {
  sid: string;
  service: string;
  channel: string;
}

export const WSGWSubscribeSchema = {
  sid: STRING,
  service: STRING,
  channel: STRING,
};
